.TapAction {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.TapAction-ActionVisible {
    background: rgba(188, 76, 78, 0.3);
}

@media only screen and (max-width : 700px) {
    .TapAction {
        cursor: pointer;
    }
}



