.AppLogo {
    display: -ms-inline-flexbox;
    display: inline-flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    height: 40px;
}

.AppLogoImage {
    background: #000;
    width: 40px;
    height: 40px;
    border-radius: 40px;
    display: inline-block;
    margin-right: 13px;
}

.AppLogoTitle {
    font-size: 22px;
    height: 100%;
    padding-top: 9px;
    letter-spacing: 0.02em;
}

@media screen and (max-width: 700px) {
    .AppLogoImage {
        display: none;
    }
}



